.tyk-info-note {
  border-radius: 0;
  position: relative;

  p + p {
    margin-block-start: var(--spacing-base);
  }

  &::after {
    content: '';
    border-style: solid;
    border-width: 0 var(--spacing-md) var(--spacing-md) 0;
    border-color: transparent var(--color-info-base) transparent transparent;
    block-size: 0;
    position: absolute;
    inset-inline-end: 0;
    inset-block-start: 0;
    inline-size: 0;
  }

  .tyk-message {
    border-radius: 0;
    padding-block: 40px;
  }
}
